<template>
  <div class="waiting-container" v-if="!isRecording">
    <h1>Meet Wingman - <span class="w1">Waiting for some signal<UnderscoreComponent></UnderscoreComponent></span></h1>
    <h2></h2>
  </div>
  <MeetComponent v-if="isRecording" :captions="captions"></MeetComponent>
</template>

<script>
import MeetComponent from "@/components/MeetComponent";
import UnderscoreComponent from "@/components/UnderscoreComponent";

export default {
  name: 'App',
  components: {
    UnderscoreComponent,
    MeetComponent
  },
  data() {
    return {
      isRecording: true,
      captions: []
    }
  },
  mounted() {
    if (window.location.pathname === '/example') {
      this.captions = [
        {
          speaker: "John Doe",
          document: "abc",
          unix: 100,
          text: "Mispellings and grammatical errors can effect your credibility. The same goes for misused commas, and other types of punctuation . Not only will Grammarly underline these issues in red, it will also showed you how to correctly write the sentence."
        },
        {
          speaker: "Jane Doe",
          document: "abc",
          unix: 100,
          text: "Underlines that are blue indicate that Grammarly has spotted a sentence that is unnecessarily wordy. You'll find suggestions that can possibly help you revise a wordy sentence in an effortless manner"
        },
      ]
    }
  },
  setup() {
    if (window.location.pathname === '/example') {
      console.log("mock here")
    }else {

      // eslint-disable-next-line no-undef
      const runtime = chrome.runtime
      runtime.onMessage.addListener(
          function(request, sender, sendResponse) {
            console.log("hello!")
            sendResponse({farewell: "goodbye"});
          }
      );
      // real data https://blog.bitsrc.io/4-ways-to-communicate-across-browser-tabs-in-realtime-e4f5f6cbedca
      console.log('setup real data')
      const channel = new BroadcastChannel('gmeet-messages');
      channel.addEventListener ('message', (event) => {
        console.log(event)
        // this.processGmeetEvent(event.data)
      });
    }

  },
  processGmeetEvent(data) {
    const date = new Date(data.date)
    if (data.kind === 'speaker') {

      this.captions.push({
        speaker: data.name,
        time:    date.toLocaleTimeString('en', {hour: "2-digit", minute: "2-digit"}),
        texts: []
      })
    }
    if (data.kind === 'text') {
      if (this.captions.length === 0) {
        return
      }
      if (data.old != null) {
        const idx = this.captions[this.captions.length-1].texts.lastIndexOf(data.old)
        if (idx >= 0) {
          this.captions[this.captions.length-1].texts[idx] = data.new
        }else {
          this.captions[this.captions.length-1].texts.push(data.new)
        }
      }else {
        this.captions[this.captions.length-1].texts.push(data.new)
      }
    }
  }
}
</script>

